import React, { memo } from 'react';
import './TagFilter.module.css';

const TagFilter = ({ allTags, selectedTags, onTagChange }) => {

  const handleTagClick = (tag) => {
    const newSelectedTags = selectedTags.includes(tag)
      ? selectedTags.filter(t => t !== tag)
      : [...selectedTags, tag];
    onTagChange(newSelectedTags);
  };
  const handleClearTags = () => {
    onTagChange([]);
  };

  return (
    <div className="tag-filter-container">
      <div className="tag-header">
        <p className="tag-filter-label">ğŸ·ï¸ áƒ¡áƒáƒ”áƒªáƒ˜áƒáƒšáƒáƒ‘áƒ˜áƒ¡ áƒ›áƒ˜áƒ®áƒ”áƒ“áƒ•áƒ˜áƒ— áƒ¤áƒ˜áƒšáƒ¢áƒ áƒ˜</p>
        {selectedTags.length > 0 && (
          <button className="clear-tags-button" onClick={handleClearTags}>
            áƒ§áƒ•áƒ”áƒšáƒáƒ¡ áƒ¬áƒáƒ¨áƒšáƒ
          </button>
        )}
      </div>
      <div className="tag-pills-container">
        {allTags.map((tag) => (
          <button
            key={tag}
            className={`tag-pill ${selectedTags.includes(tag) ? 'active' : ''}`}
            onClick={() => handleTagClick(tag)}
          >
            {tag}
          </button>
        ))}
      </div>
    </div>
  );
};

export default memo(TagFilter);